.modal {
  position: fixed;
  z-index: 2;
  top: 5%;
  left: 50%;
  background: #1c1c21;
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  margin-left: -350px;
  width: 700px;
}
.icon {
  position: absolute;
  right: 0;
  top: 0;
}
