.constructor {
  width: 600px;
  height: 30vh;
}
.order {
  display: flex;
  align-items: center;
  float: right;
}
.totalPrice {
  display: flex;
  align-items: center;
}
.constructorList {
  overflow-y: scroll;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 0;
}
.constructorList::-webkit-scrollbar {
  width: 8px;
}

.constructorList::-webkit-scrollbar-track {
  background: #2f2f37;
}

.constructorList::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}
